  border: 0px;
}

.year-select .ant-select-arrow svg {
  display: inline-block;
  height: 7px;
  width: 10px;
}

.year-select .ant-select-arrow {
  right: 10px;
}

.year-select .ant-select-focused .ant-select-selection, .year-select .ant-select-selection:focus, .year-select .ant-select-selection:active {
  border-color: transparent;
  -webkit-box-shadow: 0 0 0 1px transparent;
  box-shadow: 0 0 0 1px transparent;
}

